//source https://codepen.io/sarus/pen/PJGPmy
.photo-slider  {
    width: 100%;
    overflow-x: scroll;
    overflow-y: hidden;
    position: relative;
    padding-bottom: .5rem;
    background-color: $light_accent_gray;
    margin-top: .5rem;
    border: 1px solid $light_border_color;

    /* width */
    &::-webkit-scrollbar {
        width: 4px;
    }
    
    /* Track */
    &::-webkit-scrollbar-track {
        background: $light_gray;
        border-top: 1px $light_border_color solid;
        border-bottom: 1px $light_border_color solid;
    }
    
    /* Handle */
    &::-webkit-scrollbar-thumb {
        background: $gray;
    }
    
    /* Handle on hover */
    &::-webkit-scrollbar-thumb:hover {
        background: $primary_accent_color;
    }

    > div {
        display:flex;
        height: 50vh;
        max-height: 400px;
    }

    .image-container {
        margin-right: 1.5rem;
        height: 100%;
        width: 100%;
        max-height: 400px;
        max-width: 800px;
        display: flex;
        overflow: hidden;
        align-items: center;
    }
}

.image-container img  {
    vertical-align: middle;
    object-fit: cover;
}

.fwd.active .photo-slider div {
    transform: translateX(-40%);
}

.bck.active .photo-slider div {
    transform: translateX(-40%);
}

.image-with-credit {
    position: relative;

    .credit {
        @extend %caption_font;
        position: absolute;
        top: 40px;
        left: 0;
        padding: .5rem;
        background-color: $black;
        a {
            text-decoration: none;
            color: $light_link_color;
        }

    }
}