
$primary_accent_color: #3F5ED2;
$secondary_accent_color: #CA4747;
$primary_dark_color: darken($primary_accent_color, 5%);
$gray: #4A5968;
$light_gray: #F0F2F4;
$light_accent_gray: #E4E8EC;
$light_accent_gray_dark: darken($light_accent_gray, 5%);
$white: #FFF;
$black: #323232;

$primary_button_color: $primary_accent_color;
$primary_button_pressed_color: $primary_dark_color;
$light_gray_accent_button_pressed: $light_accent_gray_dark;
$black_text_color: $black;
$gray_text_color: $gray;
$white_text_color: $white;
$link_color: $primary_accent_color;
$light_link_color: $white_text_color;

$alert_color: $secondary_accent_color;

$serif_font: 'DM Serif Display', Garamond, serif;
$sans_serif_font: 'Poppins', Helvetica, sans-serif;

$light_border_color: #CCD4DB;

$normal_weight: 400;
$medium_weight: 500;
$bold_weight: 700;

$default-transition-duration: .5s;